import React from 'react'
import landingpage from './images/Landing.svg'
import { Link } from "react-router-dom";

function Main() {
  return (
    <div className='flex flex-col content-center mx-auto w-full'>
        <div className='text-center my-4 '><img src={landingpage} alt='Oops!' className='w-2/6 mx-auto'/></div>
        <div className='text-center my-4 '>  < Link to="/events"><button  className="rounded-xl text-white font-bold text-center p-1 w-2/6 mx-auto" style={{background: "linear-gradient(90deg, #8456EC 3.25%, #E87BF8 100%)"}}>🎉 Create my event</button></Link>
        </div>
    </div>
  )
}

export default Main