import React from "react";
import { ImCalendar, ImLocation2 } from "react-icons/im";

function Eventbox() {
  return (
    <div>
      <div className="text-left my-2 mb-20">
        <p className="text-2xl text-black font-bold">Birthday Bash</p>
        <p className="text-gray-400">
          Hosted by <span className="text-gray-600 font-semibold">Elysia</span>
        </p>
        <div className="my-2 flex flex-row">
          <div className="m-2">
            <i >
              <ImCalendar className="w-6 h-6" />
            </i>
          </div>
          <div>
            <div claasName="font-bold text-sm text-left">18 August 6:00PM</div>
            <div claasName="text-sm text-left">to 19 August 1:00PM UTC +10</div>
          </div>
        </div>
        <div className="my-2 flex flex-row">
          <div className="m-2">
            <i >
              <ImLocation2 className="w-6 h-6" />
            </i>
          </div>
          <div>
            <div claasName="text-sm text-left font-bold">Street name</div>
            <div claasName="text-sm text-left">Suburb, State, Postcode</div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Eventbox;
