import React from 'react'
import birthday from './images/Birthday.png'
import Eventbox from './Eventbox';
import Footer from './Footer';

function Events() {
  return (
    <div className='flex flex-col mx-auto justify-center  items-center w-full'>
       <div className='text-center my-4 mx-0'><img src={birthday} alt='Cake' className='w-3/6 mx-auto'/></div>
       <Eventbox />
       <hr className='w-full'/>
       <div className="sm:visible md:invisible"><Footer /></div>
    </div>
  )
}

export default Events