import React from 'react'
import Footer from './Footer'
import Header from './Header'
import Main from './Main'

function Landing() {
  return (
    <div className="bg-gray-200 w-full h-full content-center flex flex-col">
    <Header />
    <Main />
    <hr className='w-full'/>
    <div className="sm:visible md:invisible"><Footer /></div>
    </div>
  )
}

export default Landing